  #sec{
    padding: 20px;
    text-align: center;
    margin: auto;
    margin-top: 1%;
    width: 400px;
    height: auto;
    background-color: whitesmoke;
    border-radius: 20px;
    border: 5px solid white;
    box-shadow: 2px 2px 2px 2px white;
  }
  
