    width: 51%;
    margin: auto;
  }
  #inner{
    margin: auto;
    width: 80%;
    height: auto;
  }
  input[type = 'text']{
    margin: 0px;
    width: 95%;
    height: 45%;
    border-radius: 10px 0px 0px 10px;
    font-size: 16px;
    background-color: whitesmoke;
    border-top: 4px solid white;
    border-left: 4px solid white;
    border-right: none;
    border-bottom: 4px solid white;
    padding: 5px;
  }
